@use "sass:math";

body {
  @include flexbox();
  @include flex-direction(column);
  @include align-items(center);
  @include justify-content(center);
  min-height: 100vh;
  margin: 0;
  padding: 0; 
  background: whitesmoke;
}
* {
  box-sizing: border-box;
}
.row {
  @include flexbox();
  @include flex-direction(column);
  clear: both;
  width: 100%;
  @media (min-width: $screen-md) {
    @include flex-direction(row);
  }
}
.container {
  width: 100%;
  max-width: $screen-xl;
  margin: 0 auto;
}
div[class^="col-"] {
  padding: $gutter * 2;
}

// https://www.gavsblog.com/blog/for-each-while-loops-sass-scss
$columns: 16 !default;

// https://sass-lang.com/d/slash-div
@media (min-width: $screen-md) {
  @for $i from 1 through $columns {
    .col-#{$i} {
        width: math.div(100, $columns) * $i * 1%;
    }
  }
}

$marginbuffers: 5;
@for $i from 0 through $marginbuffers {
  .m-#{$i} {
    margin: $i * ($theme-font-size-base * .5);
  }
  .my-#{$i} {
    margin-top: $i * ($theme-font-size-base * .5);
    margin-bottom: $i * ($theme-font-size-base * .5);
  }
  .mx-#{$i} {
    margin-right: $i * ($theme-font-size-base * .5);
    margin-left: $i * ($theme-font-size-base * .5);
  }
  .mt-#{$i} {
    margin-top: $i * ($theme-font-size-base * .5);
  }
  .mb-#{$i} {
    margin-bottom: $i * ($theme-font-size-base * .5);
  }
  .ml-#{$i} {
    margin-left: $i * ($theme-font-size-base * .5);
  }
  .mr-#{$i} {
    margin-right: $i * ($theme-font-size-base * .5);
  }
}

$paddingbuffers: 5;
@for $i from 0 through $paddingbuffers {
  .p-#{$i} {
    padding: $i * ($theme-font-size-base * .5);
  }
  .py-#{$i} {
    padding-top: $i * ($theme-font-size-base * .5);
    padding-bottom: $i * ($theme-font-size-base * .5);
  }
  .px-#{$i} {
    padding-right: $i * ($theme-font-size-base * .5);
    padding-left: $i * ($theme-font-size-base * .5);
  }
  .pt-#{$i} {
    padding-top: $i * ($theme-font-size-base * .5);
  }
  .pb-#{$i} {
    padding-bottom: $i * ($theme-font-size-base * .5);
  }
  .pl-#{$i} {
    padding-left: $i * ($theme-font-size-base * .5);
  }
  .pr-#{$i} {
    padding-right: $i * ($theme-font-size-base * .5);
  }
}
